import resetPassStyle from "./ResetPasswordPage.module.css";

import FormBody from "../../components/FormBody/FormBody";
import {
  Button,
  Input,
  PasswordInput,
} from "@ya.praktikum/react-developer-burger-ui-components";

import { Link, Navigate, useNavigate } from "react-router-dom";
import useForm from "../../hooks/useForm";
import axios from "axios";
import { baseUrl } from "../../utils/constants";
import { useState } from "react";

const ResetPasswordPage = () => {
  const navigate = useNavigate();
  const [message, setMessage] = useState("");
  const [valueForm, handleChanges, isMessage, showMessage] = useForm({
    password: "",
    token: "",
  });

  const handlerSubmit = async (e) => {
    e.preventDefault();

    axios
      .post(`${baseUrl}/password-reset/reset`, valueForm)
      .then((res) => {
        localStorage.removeItem("emailSent");
        navigate("/login");
      })
      .catch((error) => {
        showMessage()
        setMessage("  ");
      });
  };

  return localStorage.getItem("emailSent") ? (
    <div className={`mt-30 ${resetPassStyle.colum}`}>
      <FormBody
        onSubmit={handlerSubmit}
        title=" "
        colorText="red"
        message={message}
        isMessage={isMessage}
      >
        <PasswordInput
          placeholder={"  "}
          onChange={handleChanges}
          value={valueForm?.password}
          name={"password"}
        />
        <Input
          type={"text"}
          placeholder={"   "}
          onChange={handleChanges}
          value={valueForm?.token}
          name={"token"}
          error={false}
          errorText={""}
          size={"default"}
        />
        <Button
          htmlType="submit"
          type="primary"
          size="large"
        >
          
        </Button>
      </FormBody>

      <p className="text text_type_main-default text_color_inactive mt-20">
         ?
        <Link to="/login">
          <span className="text text_type_main-default pl-2"></span>
        </Link>
      </p>
    </div>
  ) : (
    <Navigate to="/forgot-password" />
  );
};

export default ResetPasswordPage;
